.banner {
    height: 100vh;
    background: url(https://images.unsplash.com/photo-1612157777902-5382bc6e864b?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1650&q=80)center bottom;
    background-size: cover;
}

.banner-icon img {
    width: 48px;
    height: 48px;
}

.feel img {
    object-fit: cover;
    height: 420px;
    border-radius: 16px;

    @media (min-width: 768px) {
        height: 252px;
    }

    @media (min-width: 992px) {
        height: 420px;
    }
}

.teacher-card {
    border-radius: 16px;
    padding: 24px 32px;

    img {
        width: 68px;
    }
}

.plan{
    padding: 0 15px;
}

.plan-img{
    height: 250px;
    img{
        border-radius: 0px;
    }
}
